<template>
  <div class="mid">
      <img src="../../assets/image/v_0.jpg" class="toppic">
      <div class="item" v-for="(val,i) in arr">
          <img :src="val.itempic" class="itempic"/>
          <div class="bot">
              <img :src="val.icon1" class="icon1"/>
              <span>{{val.span}}</span>
