  </div>
</template>

<script>

export default {
    data:function () {
        return{
            arr:[
              {'itempic':require('../../assets/image/v_01.jpg'),'icon1':require('../../assets/image/v_02.jpg'),'span':'刘若晨','txt':700},
              {'itempic':require('../../assets/image/v_05.jpg'),'icon1':require('../../assets/image/v_06.jpg'),'span':'走进历史','txt':356},
              {'itempic':require('../../assets/image/v_07.jpg'),'icon1':require('../../assets/image/v_08.jpg'),'span':'老杨聊情怀','txt':206}
            ]
        }
    }
}
</script>


<style>
.v-main .mid .toppic{height: 75px;margin-bottom: 5px;}
.v-main .mid .item{width: 100%;box-shadow: 0 5px 5px rgba(0,0,0,0.2);margin-bottom: 5px;}
.v-main .mid .item .itempic{height: 210px;}
.v-main .mid .item .bot{height: 40px;}
.v-main .mid .item .bot .icon1{height: 30px;float: left;margin: 2px 10px 0 10px;}
.v-main .mid .item .bot .icon2{height: 20px;float: right;margin: 10px 10px 0 0 ;}
.v-main .mid .item .bot span{height: 40px;line-height: 40px;font-size: 14px;float: left;}
.v-main .mid .item .bot i{height: 40px;line-height: 40px;font-size: 14px;float: right;margin-right: 10px;}
</style>



