<template>
    <div class="mid">

        <div class="pic" v-for="(val,i) in picarr">
          <img :src="val.pic">
        </div>
    </div>
</template>

<script>
export default {
    data:function () {
        return{

          picarr:[
            {'pic':require('../../assets/image/l_8.jpg')},
            {'pic':require('../../assets/image/l_9.jpg')},
            {'pic':require('../../assets/image/l_10.jpg')},
            {'pic':require('../../assets/image/l_11.jpg')},
            {'pic':require('../../assets/image/l_12.jpg')}
          ]
        }
    },
    computed:{
        h:function () {
            return this.arr.length*40;
        }
    }
}
</script>


<style>
.l-main .mid .pic{height: 156px;width: 100%;margin-bottom: 5px;}
.l-main .mid .pic img{height: 156px;}
</style>



