<template>
    <div class="mid">
      <div class="itemA" v-for="(val,i) in itemA" v-if="val.dis">
        <img :src="val.pic">
        <h3>{{val.h3}}</h3>
        <div class="bot">
          <i>{{val.id}}</i>
          <span class="close" @click="itemA[i].dis=false">+</span>
          <span>{{val.count}}跟帖</span>
        </div>
      </div>
      <div class="itemB" v-for="(val,i) in itemB" v-if="val.dis">
        <h3>{{val.h3}}</h3>
        <div class="pic">
          <img :src="val.pic1"><img :src="val.pic2"><img :src="val.pic3">
        </div>
        <div class="bot">
          <i>{{val.id}}</i>
          <span class="close" @click="itemB[i].dis=false">+</span>
          <span>{{val.count}}跟帖</span>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data:function () {
    return{
      itemA:[
        {'h3':'变阵三后卫+激活穆里奇,谁还敢说斯科拉里团队没料','id':'体育','count':166,'pic':require('../../assets/image/hm_1.jpg'),'dis':true},
        {'h3':'于谦大醉后上台说相声,几次重大失误都让郭德纲完美遮掩','id':'小品','count':7,'pic':require('../../assets/image/hm_2.jpg'),'dis':true}
      ],
      itemB:[
        {'h3':'真辣眼,人生背后总有你没看到的故事','id':'娱乐','count':14,'pic1':require('../../assets/image/hm_3.jpg'),'pic2':require('../../assets/image/hm_4.jpg'),'pic3':require('../../assets/image/hm_5.jpg'),'dis':true},
        {'h3':'单车局势突变!摩拜与ofo供应商感受到了寒冬','id':'互联网','count':14,'pic1':require('../../assets/image/hm_6.jpg'),'pic2':require('../../assets/image/hm_7.jpg'),'pic3':require('../../assets/image/hm_8.jpg'),'dis':true},
        {'h3':'开新车去上牌,看到车牌让她很尴尬,直接不想挂车牌了','id':'汽车','count':7,'pic1':require('../../assets/image/hm_9.jpg'),'pic2':require('../../assets/image/hm_10.jpg'),'pic3':require('../../assets/image/hm_11.jpg'),'dis':true},
        {'h3':'希特勒的招牌手势,藏有什么隐情,被许多国家立法禁止使用','id':'历史','count':559,'pic1':require('../../assets/image/hm_12.jpg'),'pic2':require('../../assets/image/hm_13.jpg'),'pic3':require('../../assets/image/hm_14.jpg'),'dis':true}
      ]
    }
  }
}
</script>


<style>
.h-main .mid{width: 100%;}
hr{color: #d8d8d8;height: 1px;}
.h-main .mid .itemA,.h-main .mid .itemB{padding: 10px;border-bottom: 1px solid #d8d8d8;text-align: left;}
.h-main .mid .itemA img{float: right;width: 100px;}
.h-main .mid .itemA h3{font-weight: normal;line-height: 22px;font-size: 18px;width: 75%;color: #333333;margin-bottom: 10px;}
.h-main .mid .itemA .bot,.h-main .mid .itemB .bot{width: 75%;height: 30px;line-height: 30px;color: #b2b2b2;font-size: 14px;}
.h-main .mid .itemA .bot span,.h-main .mid .itemB .bot span{float: right;margin-right: 10px;}
.h-main .mid .itemA .bot .close,.h-main .mid .itemB .bot .close{font-family: Consolas;font-size: 26px;transform: rotate(45deg)}

.h-main .mid .itemB h3{height: 30px;font-weight: normal;font-size: 18px;}
.h-main .mid .itemB .pic{width: 100%;height: 80px;margin-bottom: 10px;}
.h-main .mid .itemB .pic img{height: 80px;margin-right: 10px;}
.h-main .mid .itemB .bot{width: 100%;}
</style>



