<template>
    <div class="mid">
        <div class="tab">
          <div class="left">
            <h3>直播预告 </h3>
            <p>直播即将开始</p>
          </div>
          <span>|</span>
          <div class="right">
              <ul :style="{height:h+'px'}">
                  <li v-for="(val,i) in arr">{{val.txt}}</li>
              </ul>
          </div>
        </div>
        <div class="pic" v-for="(val,i) in picarr">
          <img :src="val.pic">
        </div>
    </div>
</template>

<script>
export default {
    data:function () {
        return{
            arr:[
              {'txt':'全程直击网易经济学家年会'},
              {'txt':'网易经济学家年会:科技如何赋能金融'},
              {'txt':'潘石屹VS丁磊,引爆今夏最火经济峰会'},
              {'txt':'热血进行时 自由搏击 都市最强体能'},
              {'txt':'周末策划师 COS绣春刀神还原'},
              {'txt':'希腊姐妹花 爱情海怎么玩'}
            ],
          picarr:[
            {'pic':require('../../assets/image/l_8.jpg')},
            {'pic':require('../../assets/image/l_9.jpg')},
            {'pic':require('../../assets/image/l_10.jpg')},
            {'pic':require('../../assets/image/l_11.jpg')},
            {'pic':require('../../assets/image/l_12.jpg')}
          ]
        }
    },
    computed:{
        h:function () {
            return this.arr.length*40;
        }
    }
}
</script>


<style>
.l-main .mid .tab{background: white;height: 40px;padding: 10px 40px}
.l-main .mid .tab .left{float: left;text-align: left;width: 30%;height: 40px;}
.l-main .mid .tab .right{float: left;text-align: left;width: 60%;overflow: hidden;height: 40px;position: relative;}
.l-main .mid .tab h3{font-weight: normal;height: 20px;line-height: 20px;font-size: 14px;}
.l-main .mid .tab p{height: 20px;line-height: 20px;font-size: 12px;color: #a3a6aa;}
.l-main .mid .tab span{color: #d9d9d9;line-height: 40px;float: left;margin:0 10px;}
.l-main .mid .tab .right ul{position: absolute;top:0;left: 0;animation: list 10s linear infinite;}
@keyframes list {
   0%{top: 0;}
   100%{top:-220px;}
}
.l-main .mid .tab .right li{height: 40px;line-height: 20px;font-size: 14px;color: #666666;width: 100%;}

.l-main .mid .pic{height: 156px;width: 100%;margin-bottom: 5px;}
.l-main .mid .pic img{height: 156px;}
</style>



