<template>
  <div>
      <hd txt="视频"></hd>
      <div class="v-main">
        <div class="top">
          <ul :style="{width:w+'px'}">
            <li v-for="(val,i) in arr" :class="{act:num==i}" @click="num=i">
              <router-link :to="'/video/v'+i">{{val}}</router-link>
            </li>
          </ul>
        </div>
        <div>
          <router-view></router-view>
        </div>
      </div>
  </div>
</template>

<script>
import hd from '../../components/head.vue'
export default {
  data:function () {
    return{
      arr:['推荐','搞笑','影视','音乐','现场','黑科技','小品','萌物','军武','猎奇','二次元','美女','涨姿势','八卦'],
      num:0
    }
  },
  computed:{
    w:function () {
      return this.arr.length*52;
    }
  },
  components:{hd}
}
</script>


<style>
.v-main{width: 100%;padding-top: 90px;background: #f6f6f6;padding-bottom: 60px;}
.v-main .top{width: 100%;overflow: auto;height: 30px;background: white;position: fixed;left: 0;top:60px;z-index: 50;}
.v-main .top ul{height: 30px;}
.v-main .top li{float: left;height: 26px;line-height: 26px;color: #999999;font-size: 14px;padding: 0 10px;border-bottom:4px solid transparent;cursor: pointer;}
.v-main .top ul .act{border-bottom-color: #eb413d;color: #eb413d;}
.v-main .top ul .act a{color: #eb413d;}
</style>



