import Vue from 'vue'
import Router from 'vue-router'

import home from '../pages/home/home'
import h0 from '../pages/home/h0'
import h1 from '../pages/home/h1'
import h2 from '../pages/home/h2'
import h3 from '../pages/home/h3'
import h4 from '../pages/home/h4'
import h5 from '../pages/home/h5'
import h6 from '../pages/home/h6'
import h7 from '../pages/home/h7'
import h8 from '../pages/home/h8'
import h9 from '../pages/home/h9'
import h10 from '../pages/home/h10'
import h11 from '../pages/home/h11'
import h12 from '../pages/home/h12'
import h13 from '../pages/home/h13'
import h14 from '../pages/home/h14'
import h15 from '../pages/home/h15'
import h16 from '../pages/home/h16'
import h17 from '../pages/home/h17'
import h18 from '../pages/home/h18'
import h19 from '../pages/home/h19'
import h20 from '../pages/home/h20'
import h21 from '../pages/home/h21'
import h22 from '../pages/home/h22'
import h23 from '../pages/home/h23'
import h24 from '../pages/home/h24'
import h25 from '../pages/home/h25'
import h26 from '../pages/home/h26'
import h27 from '../pages/home/h27'
import h28 from '../pages/home/h28'
import h29 from '../pages/home/h29'
import h30 from '../pages/home/h30'
import h31 from '../pages/home/h31'
import h32 from '../pages/home/h32'
import h33 from '../pages/home/h33'
import h34 from '../pages/home/h34'

import news from '../pages/news/news'

import live from '../pages/live/live'
import l0 from '../pages/live/l0'
import l1 from '../pages/live/l1'
import l2 from '../pages/live/l2'
import l3 from '../pages/live/l3'
import l4 from '../pages/live/l4'
import l5 from '../pages/live/l5'
import l6 from '../pages/live/l6'
import l7 from '../pages/live/l7'
import l8 from '../pages/live/l8'
import l9 from '../pages/live/l9'
import l10 from '../pages/live/l10'
import l11 from '../pages/live/l11'
import l12 from '../pages/live/l12'

import video from '../pages/video/video'
import v0 from '../pages/video/v0'
import v1 from '../pages/video/v1'
import v2 from '../pages/video/v2'
import v3 from '../pages/video/v3'
import v4 from '../pages/video/v4'
import v5 from '../pages/video/v5'
import v6 from '../pages/video/v6'
import v7 from '../pages/video/v7'
import v8 from '../pages/video/v8'
import v9 from '../pages/video/v9'
import v10 from '../pages/video/v10'
import v11 from '../pages/video/v11'
import v12 from '../pages/video/v12'
import v13 from '../pages/video/v13'

import mine from '../pages/mine/mine'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: home,
      children:[
        {path:'',component:h0},
        {path:'h0',component:h0},
        {path:'h1',component:h1},
        {path:'h2',component:h2},
        {path:'h3',component:h3},
        {path:'h4',component:h4},
        {path:'h5',component:h5},
        {path:'h6',component:h6},
        {path:'h7',component:h7},
        {path:'h8',component:h8},
        {path:'h9',component:h9},
        {path:'h10',component:h10},
        {path:'h11',component:h11},
        {path:'h12',component:h12},
        {path:'h13',component:h13},
        {path:'h14',component:h14},
        {path:'h15',component:h15},
        {path:'h16',component:h16},
        {path:'h17',component:h17},
        {path:'h18',component:h18},
        {path:'h19',component:h19},
        {path:'h20',component:h20},
        {path:'h21',component:h21},
        {path:'h22',component:h22},
        {path:'h23',component:h23},
        {path:'h24',component:h24},
        {path:'h25',component:h25},
        {path:'h26',component:h26},
        {path:'h27',component:h27},
        {path:'h28',component:h28},
        {path:'h29',component:h29},
        {path:'h30',component:h30},
        {path:'h31',component:h31},
        {path:'h32',component:h32},
        {path:'h33',component:h33},
        {path:'h34',component:h34}
      ]
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/live',
      name: 'live',
      component: live,
      children:[
        {path:'',component:l0},
        {path:'l0',component:l0},
        {path:'l1',component:l1},
        {path:'l2',component:l2},
        {path:'l3',component:l3},
        {path:'l4',component:l4},
        {path:'l5',component:l5},
        {path:'l6',component:l6},
        {path:'l7',component:l7},
        {path:'l8',component:l8},
        {path:'l9',component:l9},
        {path:'l10',component:l10},
        {path:'l11',component:l11},
        {path:'l12',component:l12}
      ]
    },
    {
      path: '/video',
      name: 'video',
      component: video,
      children:[
        {path:'',component:v0},
        {path:'v0',component:v0},
        {path:'v1',component:v1},
        {path:'v2',component:v2},
        {path:'v3',component:v3},
        {path:'v4',component:v4},
        {path:'v5',component:v5},
        {path:'v6',component:v6},
        {path:'v7',component:v7},
        {path:'v8',component:v8},
        {path:'v9',component:v9},
        {path:'v10',component:v10},
        {path:'v11',component:v11},
        {path:'v12',component:v12},
        {path:'v13',component:v13}
      ]
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    }
  ]
})



