<template>
    <div class="child" @click="imgtab()">
        <span v-show="bol"><slot name="norimg"></slot><br/></span>
        <span v-show="!bol"><slot name="actimg"></slot><br/></span>
        <span :class="{act:!bol}">{{txt}}</span>
    </div>
</template>

<script>

export default {
  props:['txt','mark','sel'],
  methods:{
      imgtab:function(){
         this.$emit('change',this.mark);
         this.$router.push('/'+this.mark);
      }
  },
  computed:{
      bol:function(){
          if(this.mark==this.sel){
              return false;
          }
          return true;
      }
  }

}
</script>

<style>

.child{width: 20%;text-align: center;float: left;padding-top: 10px;}
.child span{height: 18px;line-height: 18px;font-size: 14px;}
.child .act{color: #eb413d;}
</style>



