<template>
  <div>
    <hd txt="直播"></hd>
    <div class="l-main">
      <div class="top">
        <ul :style="{width:w+'px'}">
          <li v-for="(val,i) in arr" :class="{act:num==i}" @click="num=i">
            <router-link :to="'/live/l'+i">{{val}}</router-link>
          </li>
        </ul>
      </div>
      <div>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import hd from '../../components/head.vue'
export default {
  data:function () {
    return{
      arr:['热门','资讯','娱乐','汽车','美女','直播号','体育','生活','健康','艺术','本地','房产','家居'],
      num:0
    }
  },
  computed:{
    w:function () {
      return this.arr.length*50;
    }
  },
  components:{hd}
}
</script>


<style>
.l-main{width: 100%;padding-top: 90px;background: #f6f6f6;padding-bottom: 60px;}
.l-main .top{width: 100%;overflow: auto;height: 30px;background: white;position: fixed;left: 0;top:60px;z-index: 50;}
.l-main .top ul{height: 30px;}
.l-main .top li{float: left;height: 26px;line-height: 26px;color: #999999;font-size: 14px;padding: 0 10px;border-bottom:4px solid transparent;cursor: pointer;}
.l-main .top ul .act{border-bottom-color: #eb413d;color: #eb413d;}
.l-main .top ul .act a{color: #eb413d;}
</style>



