<template>
    <div class="footer">
        <item txt="首页" :sel="select" mark="home" @change="getval">
            <img src="../assets/image/f1.jpg" slot="norimg">
            <img src="../assets/image/f2.jpg" slot="actimg">
        </item>
        <item txt="要闻" :sel="select" mark="news" @change="getval">
            <img src="../assets/image/f3.jpg" slot="norimg">
            <img src="../assets/image/f4.jpg" slot="actimg">
        </item>
        <item txt="直播" :sel="select" mark="live" @change="getval">
            <img src="../assets/image/f5.jpg" slot="norimg">
            <img src="../assets/image/f6.jpg" slot="actimg">
        </item>
        <item txt="视频" :sel="select" mark="video" @change="getval">
            <img src="../assets/image/f7.jpg" slot="norimg">
            <img src="../assets/image/f8.jpg" slot="actimg">
        </item>
        <item txt="我" :sel="select" mark="mine" @change="getval">
            <img src="../assets/image/f9.jpg" slot="norimg">
            <img src="../assets/image/f10.jpg" slot="actimg">
        </item>
    </div>
</template>

<script>

import item from './foot-child'

export default {
    data:function(){
            return{
                select:'home'
            }
          },
    methods:{
      getval:function(val){
          this.select=val;
      }
    },
   components:{item}

}
</script>
<style>
.footer{background: #f6f6f6;height: 60px;width: 100%;position: fixed;bottom: 0;left: 0;z-index: 50;}
.footer img{height: 25px;}
</style>




