<template>
  <div>
      <hd :src1="require('../../assets/image/h1.jpg')" :src2="require('../../assets/image/h2.jpg')" :src3="require('../../assets/image/h3.jpg')"></hd>
      <div class="h-main">
          <div class="top">
              <ul :style="{width:w+'px'}">
                  <li v-for="(val,i) in arr" :class="{act:num==i}" @click="num=i">
                      <router-link :to="'/home/h'+i">{{val}}</router-link>
                  </li>
              </ul>
              <i>></i>
          </div>
          <div>
              <router-view></router-view>
          </div>
      </div>
  </div>
</template>

<script>
import hd from '../../components/head.vue'
export default {
  data:function () {
      return{
          arr:['头条','视频','娱乐','体育','背景','网易号','薄荷','财经','科技','汽车','社会','军事','时尚','直播','图片','跟帖','NBA','热点','房产','股票','轻松一刻','段子','问吧','话题','历史','家居','独家','游戏','健康','航空','政务','彩票','漫画','美女','读书'],
          num:0
      }
  },
  methods:{

  },
  computed:{
     w:function () {
       return this.arr.length*50;
     }
  },
  components:{hd}
}
</script>


<style>
.h-main{width: 100%;padding-top: 100px;background: #f6f6f6;padding-bottom: 60px;}
.h-main .top{width: 100%;overflow: auto;height: 30px;background: white;position: fixed;left: 0;top:60px;z-index: 50;}
.h-main .top ul{height: 30px;}
.h-main .top li{float: left;height: 26px;line-height: 26px;color: #999999;font-size: 14px;padding: 0 10px;border-bottom:4px solid transparent;cursor: pointer;}
.h-main .top i{transform: rotate(90deg);position: fixed;right: 10px;top:62px;font-family: consolas;font-size: 24px;color: #999999;z-index: 10;}
.h-main .top ul .act{border-bottom-color: #eb413d;color: #eb413d;}
.h-main .top ul .act a{color: #eb413d;}
</style>



